import * as React from "react";import { createRoot } from "react-dom/client";
import {createBrowserRouter, RouterProvider,} from "react-router-dom";
import App from "./App";
import ScrollToTop from "react-scroll-to-top";
import Allexplor from "./Explor/Allexplor";
import ALLdetailes from "./detailes/ALLdetailes";
import Allauyher from "./auther/Allauyher";
import Allcreate from "./create/Allcreate";
const router = createBrowserRouter([
  {
    path: "/",
    element: <App/>, 
  },
  {
    path: "explor",
    element: <Allexplor/>,
  },
  {
    path: "detailes",
    element: <ALLdetailes/>,
  },
  {
    path: "auther",
    element: <Allauyher/>,
  },
  {
    path: "create",
    element: <Allcreate/>,
  },
]);



createRoot(document.getElementById("root")).render(
  <div>
    <ScrollToTop smooth />
    <RouterProvider router={router} />
  </div>
);
















